import './sidebar.css'
import React from 'react';

import SideNav, { Toggle, Nav, NavItem, NavIcon, NavText } from '@trendmicro/react-sidenav';


class Sidebar extends React.Component {

    constructor() {
        super()
        this.state = {
            expanded: false,
        }
    }

    render() {
        return (
            <SideNav
                onSelect={(selected) => {
                    // Add your code here
                }}
            >
                <SideNav.Toggle expanded={this.state.expanded} />
                <SideNav.Nav defaultSelected="Work Experience">
                    <NavItem eventKey="Work Experience" onClick={this.goto_experience}>
                        <NavIcon>
                            <i style={{ fontSize: '3em' }} />
                        </NavIcon>
                        <NavText>
                            Work Experience
            </NavText>
                    </NavItem>
                    <NavItem eventKey="projects">
                        <NavIcon>
                            <i style={{ fontSize: '1.75em' }} />
                        </NavIcon>
                        <NavText>
                            Projects
                        </NavText>
                    </NavItem>
                </SideNav.Nav>
            </SideNav>
        );
    }

    goto_experience() {
        document.getElementById("experience").scrollIntoView();
    }
}



export default Sidebar;